import Accordion from "./Accordion";

import {
  BlockContainer, 
  ContentContainer,
  ContentWrapperTitle,
  FaqContainer,
  Wave,
  Title,
} from "./styles";



const Faq = () => {

  return (
    <BlockContainer >

      <ContentContainer>
        
        <ContentWrapperTitle>
            <Title>FAQs</Title>
        </ContentWrapperTitle>


        <FaqContainer>
          <Accordion
            title="How can I adopt a Teddy Bear ?"
            content="
            <p>
            Simple! Connect your Phantom Wallet and follow the instructions on screen on the day we launch!
            <br />
            <br/> 1. Download the Phantom Wallet extension for your browser (Google Chrome recommended).
            <br/> 2. Buy Solana (SOL) on an exchange like Coinbase, Binance, FTX etc and send it to your Phantom wallet Public Address.
            <br/> 3. Connect your Wallet to our website by clicking on the Connect button.
            <br/> 4. Click the 'Mint for x SOL' button and approve the transaction in your Wallet.
            <br/> 5. If mint success, enjoy your Teddy!
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="When & where will it be released ?"
            content="
            <p>
            You can adopt a Teddy on December 10, 2021 at teddybearsclub.co. You will need 1 $SOL for the mint.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="Tokenomics ?"
            content="
            <p>
            A total of 1,000 Teddy Bears will be available.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="What about rarity ?"
            content="
            <p>
            Information regarding Teddy's attributes and scarcity will be published soon. 
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="Will there be a secondary market ?"
            content="
            <p>
            Once the mint is over, we will initiate the process to get listed on the secondary market as quickly as possible, more information will be released on Discord.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="Our vision..."
            content="
            <p>
            We believe in Solana and we believe in NFTs. In the future, we will regard this period as a turning point in culture and technology. History is being made, and we want to be a part of it in a meaningful way.
            <br />
            We believe that NFTs should be more than a JPEG in your portfolio. It is a cultural revolution. A new form of art accessible to all. A gathering of two wonderful worlds of art and technology.
          </p>
            "
          />
        </FaqContainer>   

      </ContentContainer>

      <Wave>
        <svg viewBox="0 0 500 80" preserveAspectRatio="none" style={{height: "100%", width: "100%", bottom: "-1px", left: "0", display: "block" }} >
          <path d="M0.00,49.98 C150.00,150.00 349.20,-50.00 500.00,49.98 L500.00,150.00 L0.00,190.00 Z" style={{stroke: "none", fill: "#111", bottom: "0"}} />
        </svg>
      </Wave>

    </BlockContainer>
  );
};

export default (Faq);
